import { useRoute } from 'vue-router';
import common from "./common.js";
// localStorage
export const setStorage = function(key, obj) {
    let json = JSON.stringify(obj);
    window.localStorage.setItem(key, json);
} 

// localStorage
export const getStorage = function(key) {
    const str = window.localStorage.getItem(key); 
    if (!str) {
        return null
    }
    return JSON.parse(str)
}

// localStorage
export const removeStorage = function(key) {
    window.localStorage.removeItem(key)
}

export const filterName = (lang)=>{
    if(!lang)return '';
    if(lang === common.LANGEN){
        return 'English';
    }else{
        return '';
    }
}

// 
export const getBrowserLang = function() {
    let browserLang = navigator.language ? navigator.language: navigator.browserLanguage;
    let defaultBrowserLang = '';
    if (browserLang.toLowerCase() === 'cn' || browserLang.toLowerCase() === 'zh' || browserLang.toLowerCase() === 'zh-cn') {
        defaultBrowserLang = 'zh-CN'
    } else {
        defaultBrowserLang = 'en-US'
    }
    return defaultBrowserLang
}

export const getRouter = () =>{
    const router = useRoute();
    return router;
}

/**
 * @param text 
 * @param id
 * @param callback
 */
export const copy = (copyEle)=>{
    let {
        id,
        msg,
        callback
    } = copyEle;
    msg = copyValue.fetchMsg(msg, id);
    try {
        copyValue.clipCopy(msg)
    } catch (e) {
        copyValue.inputCopy(msg)
    } finally {
        // 
        if (callback) callback();
    }
}

export let copyValue = {
    /**
     * @param data 
     * @desc    input 
     */
    inputCopy(data) {
        var aux = document.createElement("input");
        aux.setAttribute("value", data);
        document.body.appendChild(aux);
        aux.select();
        document.execCommand("copy");
        document.body.removeChild(aux);
    },
    /**
     * @desc  clipboard 
     */
    clipCopy(data) {
        if(!document.execCommand('copy')){
            copyValue.inputCopy(data)
            return;
        }
        let copyMethods = (e)=>{
            e.clipboardData.setData('text/plain', data);
            e.preventDefault();
        }
        document.addEventListener('copy', function copy(e) {
            copyMethods(e);
        })
        document.execCommand('copy');
        document.removeEventListener('copy', copyMethods);
    },
    /**
     * @param text 
     * @param id  DOM  id 
     * @desc ,
     *       , input  DOM 
     */
    fetchMsg(text, id) {
        if (!id) return text;
        let ele = document.querySelector('#' + id);
        const TEXT = ['INPUT', 'TEXTAREA'];
        console.log(ele.textContent);
        if (TEXT.includes(ele.tagName))
            return ele.textContent; 
        else
            return ele.innerHTML;
    }
}

export function getParamsNew(key) {
	var temData = new URLSearchParams(window.location.search);
	return temData.get(key)
}

 
//
/**
 * @param { String } uri url
 * @param { String } key key
 * @param { String } value value
 * @return { String } url
 */
export function updateQueryStringParameter(uri, key, value) {
	if(!value) {
		return uri;
	}
	var re = new RegExp("([?&])" + key + "=.*?(&|$)", "i");
	var separator = uri.indexOf('?') !== -1 ? "&" : "?";
	if (uri.match(re)) {
		return uri.replace(re, '$1' + key + "=" + value + '$2');
	}
	else {
		return uri + separator + key + "=" + value;
	}
}