import { ElLoading, ElMessage, ElConfigProvider } from "element-plus";
import {
  Folder,
  Share,
  Notebook,
  WalletFilled,
  OfficeBuilding,
  Lock,
  QuestionFilled,
  ShoppingCart,
  ArrowDown,
  Setting,
  Postcard,
  Star,
  Switch,
  SwitchButton,
  CopyDocument,
  SuccessFilled,
  WarnTriangleFilled,
  Plus,
  Edit,
} from "@element-plus/icons-vue";
/**
 *  Element Plus 
 * Vite  https://github.com/antfu/unplugin-vue-components
 * @param app {App}
 */

export default {
  install: (app, options) => {
    // 
    const components = [ElLoading, ElMessage, ElConfigProvider];
    const icon = [
      WarnTriangleFilled,
      SuccessFilled,
      Folder,
      Share,
      Notebook,
      WalletFilled,
      OfficeBuilding,
      Lock,
      QuestionFilled,
      ShoppingCart,
      ArrowDown,
      Setting,
      Postcard,
      Star,
      Switch,
      SwitchButton,
      CopyDocument,
      Plus,
      Edit,
    ];
    icon.map((v) => {
      app.component(v.name, v);
    });
    components.forEach((v) => {
      app.use(v);
    });
    return app;
  },
};
